<template>
<!-- 项目评估 -->
  <div class="left">
      <div class="title">
        <img src="../../assets/images/icon/icon_小标题.png" class="pscore">
        各省得分情况
      </div>
      <div>
          <el-table
            :data="tableData"
            style="width: 100%"
            :height="height"
            >
            <el-table-column
              prop="date"
              label="省份"
              >
            </el-table-column>
            <el-table-column
              prop="name"
              label="最低分"
              >
            </el-table-column>
            <el-table-column
              prop="address"
              label="最高分">
            </el-table-column>
            <el-table-column
              prop="address"
              label="平均分">
            </el-table-column>
          </el-table>
      </div>
  </div>
</template>
<script>
import { getwh } from '../../util/utils.js';
export default {
  components: {
  },
  data() {
    return {
      height:0,
      screenHeight:0,
      tableData:[]
    };
  },
  mounted(){
        let arrhw=getwh()
         this.screenHeight = arrhw[1];
          window.onresize = () => {
          return (() => {
            arrhw=getwh()
            this.screenHeight = arrhw[1];
          })();
        };
        this.$nextTick(()=>{
          this.height= (this.screenHeight-200)
        })
  },
  methods: {
   
  },
  
};
</script>
<style  scoped>
.left {
  margin: 10px;
  box-shadow: 0px 2px 8px rgba(200, 201, 204, 0.5);
 
}
 .title{
    background: url("../../assets/images/小标题.png") no-repeat;
    color: #ffffff;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
  }
.pscore{
  vertical-align: middle;
}

</style>



