import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import water from '@/components/water'
import proapp from '@/components/projectapp/index'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    // proapp
    {
      path: '/',
      name: 'proapp',
      component: proapp
    },
    {
      path: '/water',
      name: 'water',
      component: water
    }
  ]
})



