<template>
  <div class="water">
    <div class="wa" ref="wa"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import 'echarts-liquidfill'
export default {
  name: '',
  data () {
    return {
     data:[0]
    }
  },
  mounted(){
       let i=0
          let perc = setInterval(()=>{
              i++
              this.data=[i/100]
              this.init(i)
              if(i>=100){
                i=100
                
                clearInterval(perc)
              }
            },100);
  },
  methods:{
    init(i){
      let wa = this.$refs.wa
      let myChart = echarts.init(wa)
        let option = {
             backgroundColor: '#0F224C', //背景色
             title: {
        show: true,
        text: '评估中',
        x: '50%',
        y: '60%',
        z: 10,
        textAlign: 'center',
        textStyle: {
            color: '#ffffff',
            fontSize: 30,
        },
        },
        series: [
            {
                type: 'liquidFill', //水位图
                radius: '80%', //显示比例
                center: ['50%', '50%'], //中心点
                amplitude: 10, //水波振幅
                data: this.data, // data个数代表波浪数
                color: [
                    {
                        type: 'linear',
                        x: 0,
                        y: 0,
                        x2: 0,
                        y2: 1,
                        colorStops: [
                            {
                                offset: 0,
                                color: '#446bf5',
                            },
                            {
                                offset: 1,
                                color: '#2ca3e2',
                            },
                        ],
                        globalCoord: false,
                    },
                ], //波浪颜色
                backgroundStyle: {
                    borderWidth: 1, //外边框
                    // borderColor: '#23cc72', //边框颜色
                    color: 'RGBA(51, 66, 127, 0.7)', //边框内部填充部分颜色
                },
                label: {
                    //标签设置
                    position: ['50%', '45%'],
                    formatter: ()=>{
                    console.log(i)
                      return i+"%";
                    }, //显示文本,
                    textStyle: {
                        fontSize: '52px', //文本字号,
                        color: '#fff',
                    },
                },
                outline: {
                    // show: false
                    borderDistance: 0,
                    itemStyle: {
                        borderWidth: 2,
                        borderColor: '#112165',
                    },
                },
            },
        ],
        }
        myChart.setOption(option);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.wa{
  width: 500px;
  height: 600px;
}
</style>



