/**
 *  常用工具类
 */


/**
 * 获取宽高
 * @param {string} null
 */
 export function getwh () {
    let w=document.body.clientWidth;
    let h=document.documentElement.clientHeight;
    return [w,h];
}

/**
 * 下载
 * @param {string} null
 */
 export function down (data,name) {
     if(name==""){
         name="demo"
     }
    const link = document.createElement("a");
    const blob = new Blob([data], {
      type: "application/excle",
    });
    link.style.display = "none";
    link.href = URL.createObjectURL(blob);
    link.setAttribute("download", name+".xls");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
}



/**
 * 获取数据
 * @param {string} data
 */
 export function getinit (data) {
   let lists = window.localStorage.getItem("nameList");
   let arr = JSON.parse(lists);
   let colums=[];
   arr.forEach(item=>{
       for(var key in data){
          if(item.zbmc.toUpperCase()==key){
            colums.push({name:item.description,value:key})
          }
        }
  })
    return colums;
}



 
export function qxdata (data,val) {
    data.forEach(item=>{
        item.value.forEach(item2=>{
            if(item2.zbmc==val.zbmc){
                item2.checked=false
            }
        })
    })
    return data;
}



