<template>
<!-- 项目评估 -->
  <div class="center">
      <div class="topbox">
          <div class="title">
            <img src="../../assets/images/icon/icon_小标题.png" class="pscore">
            综合评分情况
          </div>
          <div class="tip">评分规则说明 <i class="el-icon-warning-outline yell" @click="info"></i></div>
          <div class="cbox">
              <div class="onecont">
                <div class="title1">综合评分结果</div>
                <div class="oscores">85</div>
                <div class="oimg"><img src="../../assets/images/oline.png" alt=""></div>
                <div class="subt">平均得分</div>
              </div>
            <div ref="charttop1" class="charttop" :style="{height:height,width:width}"></div>
          </div>
          <div class="cbox">
            <div class="ctop">应评估：1000 &nbsp;&nbsp;&nbsp;<img src="../../assets/images/cir.png" alt=""> 已评估：800</div>
             <div class="onecont">
                <div class="oscores tscor">65%</div>
                <div class="oimg"><img src="../../assets/images/tline.png" alt=""></div>
                <div class="subt tsub">评分进度</div>
              </div>
            <div ref="charttop2" class="charttop" :style="{height:height,width:width}"></div>
            <div class="rbtn"><el-button type="success">一键评估</el-button></div>
          </div>
      </div>
      <div class="botbox">
          <div class="title">
            <img src="../../assets/images/icon/icon_小标题.png" class="pscore">
            评分分布
          </div>
          <div ref="chartbt" class="chartbt" :style="{height:height1,width:widtha}"></div>
      </div>
  </div>
</template>
<script>
import * as echarts from "echarts";
import { getwh } from '../../util/utils.js';
export default {
  components: {
  },
  data() {
    return {
      height:"",
      height1:"",
      screenHeight:"",
      width:"",
      screenWidth:"",
      widtha:"",
      heighte:"",
      widthe:"",
      left1:""
    };
  },
  mounted(){
         let arrhw=getwh()
         this.screenHeight = arrhw[1];
         this.screenWidth = arrhw[0];
          window.onresize = () => {
          return (() => {
            arrhw=getwh()
            this.screenHeight = arrhw[1];
            this.screenWidth = arrhw[0];
          })();
        };
         this.width = (this.screenWidth*22/100)+'px'
          this.height= (this.screenHeight*35/100)+'px'
          this.height1= (this.screenHeight*39/100)+'px'
          this.widtha = this.screenWidth*44/100 + "px"
        this.$nextTick(()=>{
         
          this.init()
        })
        
  },
  methods: { 
    // 评估指标在同一监测期内按最差至最优项目进行排序（负向指标按值从大到小排序，正向指标按值从小到大排序），然后进行分段，取当前项目排序占总数的比值，乘以100得到指标的相对分值。',
    info(){
        this.$alert('负向指标按值从大到小排序，正向指标按值从小到大排序',
       
         '评分规则说明', {
          confirmButtonText: '确定'
        });
    },
    init(){
      this.chart1()
      this.chart2()
      this.chart3()
    },
    chart1(){
      let myChart = echarts.init(this.$refs.charttop1);
      let option = {
          color:["#D6E2F5","#346DFF"],
          series: [
            {
              type: 'pie',
              top: '-10%',
               label: {
                  show: false
                },
              radius: ['72%', '75%'],
              data: [
                { value: 15 },
                { value: 85 },
              ]
            }
          ]
        }
        myChart.setOption(option);
    },
     chart2(){
      let myChart = echarts.init(this.$refs.charttop2);
      let option = {
          color:["#D6E2F5","#73F089"],
          series: [
            {
              type: 'pie',
               label: {
                  show: false
                },
                top: '-15%',
              radius: ['45%', '50%'],
              data: [
                { value: 350 },
                { value: 650 },
              ]
            }
          ]
        }
        myChart.setOption(option);
    },
     chart3(){
      let myChart = echarts.init(this.$refs.chartbt);
      let option = {
        color:["#5FBBFF","#FF7734","#FFE566","#638DF8"],
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
              }
            },
            legend: {},
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            xAxis: {
              type: 'value',
                   axisTick:{
              show:false
            },
            axisLine:{
              show:false
            },
            },
            yAxis: {
              type: 'category',
              data: ['交通', '能源', '水利', '其他'],
              splitLine: {
                      show: false
                  },
                   axisTick:{
              show:false
            },
            axisLine:{
              show:false
            },
            },
            series: [
              {
                name: '<70',
                 stack: 'total',
                type: 'bar',
                label: {
                  show: true
                },
                barWidth:20,
                data: [320, 302, 301, 334]
              },
              {
                name: '70-80',
                stack: 'total',
                type: 'bar',
                label: {
                  show: true
                },
               barWidth:20,
                data: [120, 132, 101, 134]
              },
              {
                name: '80-90',
                type: 'bar',
                stack: 'total',
                label: {
                  show: true
                },
                barWidth:20,
                data: [220, 182, 191, 234]
              },
              {
                name: '>90',
                type: 'bar',
                stack: 'total',
                label: {
                  show: true
                },
                barWidth:20,
                data: [20, 102, 19, 24]
              }
            ]
          }
        myChart.setOption(option);
    }
  },
  
};
</script>
<style scoped>
.center {
  margin: 10px;

  
}

   .title{
    background: url("../../assets/images/底2.png") no-repeat;
    color: #ffffff;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
  }
  .pscore{
    vertical-align: middle;
  }
  .topbox{
    box-shadow: 0px 2px 8px rgba(200, 201, 204, 0.5);
    height: 38vh;
  }
  .cbox{
    display: inline-block;
    position: relative;
  }
  .botbox{
    margin-top: 10px;
    box-shadow: 0px 2px 8px rgba(200, 201, 204, 0.5);
    height: 41vh;
  }
  .charttop{
    display: inline-block;
  }
  .tip{
    margin: 12px 0 0 16px;
    font-weight: 400;
    font-size: 14px;
  }
  .yell{
    color: #FFC164;
    cursor: pointer;
  }
  
  .ctop{
    text-align: center;
    position: absolute;
    width: 100%;
    margin-top: 10px;
  }
  .rbtn{
    text-align: center;
    position: absolute;
    width: 100%;
    bottom: 14%;
  }
  .onecont{
    position: absolute;
    left: 36%;
    top: 25%;
   
  }
   .title1{
      font-weight: 400;
      font-size: 20px;
      line-height: 26px;
      text-align: center;
    }
    .oscores{
      background: rgba(87, 164, 255, 0.1);
      box-shadow: inset 0px 0px 4px #4897FF;
      border-radius: 4px;
      width: 90.83px;
      height: 49.74px;
      line-height: 49.74px;
      color: #3F6AF0;
      text-align: center;
      margin-left: 13%;
      margin-top: 20%;
      font-weight: 700;
      font-size: 39px;
    }
    .oimg{
      margin-top: 8%;
      margin-left: 5%;
    }
    .subt{
      text-align: center;
    }
    .tscor{
      background: rgba(87, 255, 134, 0.1);
      box-shadow: inset 0px 0px 4px #48FF7B;
      border-radius: 4px;
      color: #67ED7E;
    }
    .tsub{
      margin-left: 6%;
    }
</style>



