<template>
<!-- 项目评估 -->
  <div class="wrap">
      <el-row>
        <el-col :span="24">
          <el-col :span="22">
              <searchs :isdrawer="isdrawer" ></searchs>
          </el-col>
          <el-col :span="2">
            <div>
              <el-button type="primary" class="right" @click="show">评估详情</el-button>
            </div>
          </el-col>
        </el-col>
      </el-row>
      <el-row>
          <el-col :span="24">
            <el-col :span="6">
              <left></left>
            </el-col>
            <el-col :span="12">
             <center></center>
            </el-col>
            <el-col :span="6">
             <right></right>
            </el-col>
          </el-col>
      </el-row>
      <el-drawer
        :visible.sync="drawer"
        :direction="direction"
        :modal="model"
        :show-close="sclose"
        :with-header="false"
        size="100%">
        <drawer-madol @closed="handleClose"></drawer-madol>
      
      </el-drawer>
  </div>
</template>
<script>
import searchs from './searchs'
import left from './left.vue'
import center from './center.vue'
import right from './right.vue'
import drawerMadol from './drawerMadol' 
export default {
  components: {
    searchs,
    right,
    left,
    center,
    drawerMadol
  },
  data() {
    return {
      drawer:false,
      direction:"rtl",
      model:false,
      sclose:false,
      isdrawer:false
    };
  },
  mounted(){
    
  },
  methods: {
    handleClose(){
      this.drawer=false
    },
    show(){
      this.drawer=true
    }
  },
  
};
</script>
<style  >
.wrap {
  height: 100vh;
  padding: 10px 20px 0 20px;
  text-align: left;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
}
.right{
  float: right;
}
.el-drawer__wrapper{
  /* margin-top: 80px; */
}

</style>



